import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

//
const store =  new Vuex.Store({
    //
    state: {
        count: '',
        num: 1
    },
    getters: {
        doneTodos: state => {
            return state.num + 1000;
        }
    },
    //
    mutations: {
        increment (state,status){
            state.count = status
        }
    },

    actions: {
        increment (context) {
            context.commit('increment')
        }
    }
})

//
export default store


